/* eslint-disable */
export default (conf) => (httpClient, serializeParams) => {
  // Base param constants
  const baseUri = conf.baseUri || '';

  const version = 'v2';


  /**
   * The root defined types from the RAML.
   * @private
   */
  const definedTypes = {};

  

  

  /*
   * @name parse
   * @type {Function}
   * @private
   * @description Should be overwritten by transformResponse on a project level
   */
  function parse(res) {
    return {
      data: res.data,
      headers: res.headers,
      status: res.status,
      statusText: res.statusText,
    };
  }

  
  /**
   * @name PlacesData#getPlaces
   * @type {Function}
   * @description Retrieve list of all places.
   
   * @param {Object} params Map of query parameters.
     
   * @param {number} params.latitude Latitude for current location. Should be used with longitude and radius params to return places available in specified radius. Eg: 40.71558.
     
   * @param {number} params.longitude Longitude for current location. Should be used with latitude and radius params to return places available in specified radius. Eg: 39.91558.
     
   * @param {number} params.radius Search radius (distance in KM). Eg: 2.232.
     
   
   * @param {?Object} headers Map of custom header attributes.
     
   
   * @returns {Promise.<Response>}
   *
   * @example
   * placesData
   *  .getPlaces(params, headers)
   *  .then(function(result){
   *    console.log('headers', result.headers)
   *    console.log('data', result.data);
   *  });
   */
  function getPlaces(params, headers) {
    const url = `${baseUri}${version}/places`;

    return httpClient({
      method: 'GET',
      url,
      
      params: params || {},
      
      headers: headers || {},
    }).then(parse)
    .catch(err => { throw parse(err); });
  }
  

  

  
  const schemas = {};
  

  /**
   * @typedef Response
   * @type {Object}
   * @property {Object} data See method descriptions for possible return types
   * @property {Function} headers Getter headers function
   * @property {Number} status HTTP status code of the response.
   * @property {String} statusText HTTP status text of the response.
   */

  return ({
    
    getPlaces,
    
    
    schemas,
  });
};



